import * as types from '../actions/actionTypes';

const INITIAL_STATE = {
  loading: '',
};

export default function sessionReducer(state = INITIAL_STATE, action) {
  switch (action.type) {
    case types.SESSION_FETCH:
      console.log('Session fetch.');
      return {
        // ...state == the non explicitly stated parts of the state
        ...state,
        loading: true,
      };

    default:
      return state;
  }
}



