import * as types from './actionTypes';
import sessionApi from '../api/sessionApi';

function sessionSuccess() {
  return { type: types.SESSION_SUCCESS };
}

function sessionFailure() {
  return { type: types.SESSION_FAILURE };
}

function sessionFetch() {
  return { type: types.SESSION_FETCH };
}

export function getSessions() {
  return (dispatch) => {
    dispatch(sessionFetch());
    return sessionApi
      .getSessions()
      .then((responseJson) => {
        console.log(responseJson);
        dispatch(sessionSuccess());
      })
      .catch((error) => {
        console.error(error);
        dispatch(sessionFailure());
      });
  };
}



